@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
*{margin:0;padding:0}body{font-family:"Roboto",sans-serif;color:#616370}
.App .comment{opacity:.5;color:gray;font-size:22px;font-weight:bold;position:fixed;left:50px}.App .container{background-color:#e9eaec;height:100vh;display:flex;align-items:center;justify-content:center}.App .container .content-wrapper{box-shadow:11px 11px 10px 0px rgba(195,205,211,.2) inset,-11px -11px 10px 0px rgba(255,255,255,.5) inset;border-radius:25px;padding:20px 20px 50px}.App .container .content-wrapper .subtitle{font-size:35px;margin:0 0 20px 30px}.App .container .content-wrapper .types{display:flex;flex-wrap:wrap;justify-content:center}.App .container .content-wrapper .types .btn{min-width:180px;width:180px;max-width:180px;box-shadow:inset 0 0 0 transparent,inset 0 0 0 transparent,11px 11px 10px 1px rgba(32,66,87,.2),-11px -11px 10px 0px rgba(255,255,255,.5);background:#e9eaec;border-radius:30px;padding:20px 40px;flex:1 1;cursor:pointer;margin:15px 30px;transition:box-shadow .3s ease;text-align:center}.App .container .content-wrapper .types .btn .type-icon{font-size:70px;line-height:.5}.App .container .content-wrapper .types .btn .type{transition:all .3s ease;margin:10px 10px;font-size:18px;font-weight:bold;text-align:center;white-space:nowrap}.App .container .content-wrapper .types .btn .estimated-time{font-family:"Rubik";font-size:18px;white-space:nowrap}.App .container .content-wrapper .types .btn .estimated-price{margin:60px 0 10px 0;font-size:25px;font-weight:bold;white-space:nowrap}.App .container .content-wrapper .types .btn.selected{cursor:default;box-shadow:11px 11px 15px 0px rgba(32,66,87,.2) inset,-11px -11px 15px 0px rgba(255,255,255,.5) inset}.gradient-text{background:linear-gradient(90deg, #12c3fa 30%, #0461ea 70%);-webkit-background-clip:text;background-clip:text;color:transparent}
